import React from "react";
import { Route, Switch } from "react-router-dom";
import LayoutUser from "../component/layout/LayoutUser";
import Covid from "../pages/covid/Covid";
import Home from "../pages/home/Home";
import Login from "../pages/login/Login";
import Register from "../pages/register/Register";
import Yearbook from "../pages/yearbook/Yearbook";

const AppRuote = ({ layout: Layout, component: Component, ...rest }) => (
  <Route
    {...rest}
    render={(props) => (
      <Layout>
        <Component {...props}></Component>
      </Layout>
    )}
  ></Route>
);

const Router = () => {
  return (
    <div>
      <Switch>
        <AppRuote
          path="/"
          exact={true}
          layout={LayoutUser}
          component={Home}
        ></AppRuote>
        <AppRuote
          path="/login"
          exact={true}
          layout={LayoutUser}
          component={Login}
        ></AppRuote>
        <AppRuote
          path="/covid"
          exact={true}
          layout={LayoutUser}
          component={Covid}
        ></AppRuote>
        <AppRuote
          path="/register"
          exact={true}
          layout={LayoutUser}
          component={Register}
        ></AppRuote>
        <AppRuote
          path="/yearbook"
          exact={true}
          layout={LayoutUser}
          component={Yearbook}
        ></AppRuote>
      </Switch>
    </div>
  );
};

export default Router;
